@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
header{
    height: 100vh;
}
.btn{
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    border: 1px solid #3C4880;
    cursor: pointer;
    color: #fff;
    font-weight: 600;
}
.btn-2{
    background-color: #3C4880;
    color: #fff;
}
.me{
    width: 28rem;
    height: auto;
    border-radius: 40px;
}
.imageMe{
    justify-content: center;
    width: 100%;
    display: flex;
    margin-top: 2rem;
}
.cta{
    margin-top: 3rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}
.container{
    justify-content: center;
    margin-top: 4rem;
}
.container h2{
    text-align: center;
    font-size: 1.4rem;
}
.container h1{
    text-align: center;
    font-size: 2rem;
}
.container p{
    text-align: center;
    font-size: 1.2rem;
}


nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 999;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a{
    background: transparent;
    padding: 0.7rem;
    border-radius: 50%;
    display: flex;
    color: #fff;
    font-size: 1.1rem;
}
nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}
nav a.active{
    background: rgba(0, 0, 0, 0.3);
    
}
.about_container{
    display: grid;
    grid-template-columns:  50% 50%;
    grid-gap: 0%;
    gap: 0%;
    margin-top: 4rem;
}
.imageCol{
    /* width: 100%; */
    /* aspect-ratio: 1/1; */
    border-radius: 40px;
    /* display: grid;
    place-items: center; */
}
.imgContainer
{
    width: 100%;
    display: grid;
    place-items: center;
}
.contentCol{
    display:flex;
    flex-direction: column;
    justify-content: center;
    vertical-align: middle;
    margin-right: 20%;
}
.contentCol p{
    margin: 2rem 0 2rem;
}
.imageBg{
    height: 21rem;
    aspect-ratio: 1/1;
    background-color: #3C4880;
    border-radius: 40px;
}
.imageCol img{
    border-radius: 40px;
    width: 21rem;
    transform: rotate(8deg);
    transition: 0.6s;
}
.imageCol img:hover{
    transform: rotate(0);
}
.about_header{
    align-items: center;
    text-align: center;
    margin-top: 100px;
}
.about_cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}
.contentCol p{
    color: #000;
    font-weight: 500;
}
.about_card{
    background: #3C4880;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}
.about_card h5{
    color: #5dd9f8;
}
.about_card small{
    color: rgb(170, 170, 170);
}
.about_icon{
    font-size: 1.6rem;
    margin-bottom: 0.8rem;
    color: #fff;
}
.about_header h3{
    color: #5dd9f8;
}
.about_header h2{
    color: #fff;
}
.skills_header{
    align-items: center;
    text-align: center;
    margin-top: 60px;
    margin-bottom: 5rem;
}
.skills_header h3{
    color: #5dd9f8;
}
.skills_header h2{
    color: #fff;
}
.skills_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 4rem;
    gap: 4rem;
    margin-left: 16rem;
    margin-right: 16rem;
}
.skills_container > div{
    background: #3C4880;
    padding:  2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    color: #fff;
}
.skills_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: #5dd9f8;
}
.skills_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}
.skills_detail{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.skills_detail_icons{
    margin-top: 6px;
    color: #5dd9f8;
}
.text-light{
    color: rgb(170, 170, 170);
}
.contact_header{
    align-items: center;
    text-align: center;
    margin-top: 60px;
    margin-bottom: 5rem;
}
.contact_header h3{
    color: #5dd9f8;
}
.contact_header h2{
    color: #fff;
}
.contact_container{
    width: 100%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}
.contact_options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    /* align-content: center;
    justify-content: center; */
}
.contact_option{
    background: #3C4880;
    padding: 1.6rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: 0.6s;
}
.contact_option:hover{
    background: #44528f;
    border-color: #3C4880;
}
.contact_option h4{
    color: #fff;
    font-size: 1.2rem;
}
.contact_option h5{
    color: #fff;
    font-size: 1rem;
}
.contact_icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    color: #fff;
}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 1rem;
    text-decoration: none;
    color: #5dd9f8;
}

/*=====================FORM====================*/
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
input,textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid #3C4880;
    resize: none;
    color: #fff;
    font-size: 1.2rem;
}
::-webkit-input-placeholder{
    color: #fff;
    font-size: 1.2rem;
}
:-ms-input-placeholder{
    color: #fff;
    font-size: 1.2rem;
}
::-ms-input-placeholder{
    color: #fff;
    font-size: 1.2rem;
}
::placeholder{
    color: #fff;
    font-size: 1.2rem;
}
.contact_button_send{
    padding: 1.2rem !important;
    font-size: 1rem;
    width: 30%;
    float: right;
}
.portfolio_header{
    align-items: center;
    text-align: center;
    margin-top: 60px;
    margin-bottom: 5rem;
}
.portfolio_header h3{
    color: #5dd9f8;
}
.portfolio_header h2{
    color: #fff;
}
.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}
.portfolio_item{
    background: #3C4880;
    padding: 1.3rem;
    border-radius: 2rem;
    margin: 0 auto;
}
.portfolio_item h3{
    display: flex;
    padding-bottom: 1rem;
}
.portfolio_item_image img{
    width: 22rem;
    border-radius: 1.5rem;
    overflow: hidden;
}

.btn_portfolio{
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    border: 1px solid #000000;
    cursor: pointer;
    color: #fff;
    font-weight: 600;
}
.btn-2_portfolio{
    background-color: #000000;
    color: #fff;
}
.portfolio_item_cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.5rem;
}
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
html{
    scroll-behavior: smooth;
    overflow: hidden;
}
body{
    font-family: "Poppins", sans-serif;
    line-height: 1.7;
    background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}
section{
    height: 100vh;
    padding-top: 5rem;
}
.container_0{
    margin: 0 auto;
    width: 75%;
}

